@import "../../scss/variables";

#main {
	section {
		height: 450px;
		-webkit-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		transform-style: preserve-3d;
		>.text {
			position: relative;
			top: 52.5%;
			transform: translateY(-50%);
			z-index: 2;
			>h1 {
				font-size: 42px;
				color: #fff;
				border-bottom: 0;
				margin: 0 0 10px;
			}
			>h3 {
				color: #fff;
				margin: 0;
			}
			h1 {
				font-size: 2.5rem;
				font-weight: 400;
			}
		}
	}
	text-align: left;
}
#more {
	margin: 20px auto 48px;
}
@media (max-width:991px) {
	#main {
		section {
			>.text {
				position: relative;
				top: 50.5%;
				transform: translateY(-50%);
				z-index: 2;
			}
		}
	}
}
@media (max-width: 576px) {
	#main {
		section {
			>.text {
				h1 {
					font-size: 1.5rem !important;
					font-weight: 500 !important;
				}
			}
		}
	}
	.header-text {
		p {
			font-size: 14px;
			font-weight: 300 !important;
		}
	}
}
